$rivergray :   blue;
.block-views-riverton-videos-block  {
  width: 50%;
  .view-id-riverton_videos #video {
    margin-bottom: 0px;
  }
  .block-title {
    font-size: 2rem;
    line-height: 2rem;
    margin-top: 2rem;
  }
  .view-header {
    font-family: 'Open Sans', Helvetica, Arial, sans-serif;
    font-weight: 600;
    font-size: 1.5rem;
    line-height: 1.5pxrem;
    margin-top: 1rem;
    color: $rivergray;
  }
  .flexslider .slides {
    zoom: 2;
  }
  .flexslider.optionset-video-nav {
    margin: 0px;
    ul {
      width: 80% !important;
      margin: 0px auto;
      display: flex;
      flex-wrap: nowrap;
      flex-direction: row;
    }
    li {
      font-size: .71rem;
      text-align: center;
      width: auto !important;
      flex-grow: 1;
    }
    li:hover {
      color: green;
    }

    li {
      border-right: 1px solid black;
    }
    li:last-of-type {
      border-right: none;
    }
  }
}